/*
 * CSS Styles that are needed by jScrollPane for it to operate correctly.
 *
 * Include this stylesheet in your site or copy and paste the styles below into your stylesheet - jScrollPane
 * may not operate correctly without them.
 */

.jspContainer {
    overflow: hidden;
    position: relative;
}

.jspPane {
    position: absolute;
}

.jspVerticalBar {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 6px;
    height: 100%;
    background-color: #f0f0f0;

    .shops-map__list &{
        top: 0; right: 50px;
        background-color: rgba(#004c93, .12);
        @include media('<1200px'){
            right: 10px;
        }
    }
}

.jspHorizontalBar {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 12px;
    border-radius: 4px;
    background-color: #fff;
}

.jspScrollable {
    &:focus {
        outline: none;
        border: 0;
    }
}

.jspCap {
    display: none;
}

.jspHorizontalBar .jspCap {
    float: left;
}

.jspTrack {
    background-color: #f0f0f0;
    position: relative;
    width: 6px;
    border-radius: 3px;
    .shops-map__list &{
        background-color: rgba(#004c93, .12);
    }
    .compare &{
        background-color: #ffffff;
    }
}

.jspDrag {
    background-color: #c4c4c4;
    position: relative;
    top: 0;
    left: 0;
    width: 6px;
    border-radius: 3px;
    cursor: pointer;
    .shops-map__list &{
        background-color: #004c93;
    }
    .compare &{
        background-color: $color__primary;
    }
}

.jspHorizontalBar .jspTrack,
.jspHorizontalBar .jspDrag {
    float: left;
    height: 100%;
}

.jspArrow {
    background: #50506d;
    text-indent: -20000px;
    display: block;
    cursor: pointer;
    padding: 0;
    margin: 0;
}

.jspArrow.jspDisabled {
    cursor: default;
    background: #80808d;
}

.jspVerticalBar .jspArrow {
    height: 17px;
}

.jspHorizontalBar .jspArrow {
    width: 17px;
    float: left;
    height: 100%;
}

.jspVerticalBar .jspArrow:focus {
    outline: none;
}

.jspCorner {
    background: #eeeef4;
    float: left;
    height: 100%;
}


/* Yuk! CSS Hack for IE6 3 pixel bug :( */

* html .jspCorner {
    margin: 0 -3px 0 0;
}